////
/// Animations
/// @group components
////


@each $direction in $animation-directions {
    @keyframes fade-#{$direction} {
        from {
            @if ($direction == in) {
                opacity: 0;
            }
            @else {
                opacity: 1;
            }
        }
        to {
            @if ($direction == in) {
                opacity: 1;
            }
            @else {
                opacity: 0;
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $axis in $animation-axes-directions {
        @keyframes rotate-#{$axis}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($axis == horizontal) {
                        transform: rotateY(-90deg);
                    }
                    @else {
                        transform: rotateX(-90deg);
                    }
                }
                @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                }
                @else {
                    @if ($axis == horizontal) {
                        transform: rotateY(-90deg);
                    }
                    @else {
                        transform: rotateX(-90deg);
                    }
                }
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $axis in $animation-axes-directions {
        @keyframes scale-#{$axis}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($axis == horizontal) {
                        transform: scaleY(0);
                    }
                    @else {
                        transform: scaleX(0);
                    }
                }
                @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                }
                @else {
                    @if ($axis == horizontal) {
                        transform: scaleY(0);
                    }
                    @else {
                        transform: scaleX(0);
                    }
                }
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $slide, $value in $animation-cardinal-directions {
        @keyframes slide-#{$slide}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($slide == top or $slide == bottom) {
                        transform: translateY(#{$value});
                    }
                    @else {
                        transform: translateX(#{$value});
                    }
                }
                @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                }
                @else {
                    @if ($slide == top or $slide == bottom) {
                        transform: translateY(#{$value});
                    }
                    @else {
                        transform: translateX(#{$value});
                    }
                }
            }
        }
    }
}

////
//Animation reveal
/////


$animation-duration: 1s;

.animation-reveal {
    background-color: transparent;
    width: 100%;
    height: auto;
    position: relative;

    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        animation-duration: $animation-duration;
        animation-timing-function: ease;
        animation-direction: both;
    }

    &::before {
        background-color: $color-prussian;
    }

    &::after {
        background-color: white;
        animation-delay: ($animation-duration / 3);
    }

    .animation-reveal__content {
        opacity: 0;
    }
}

.animation-reveal__load--right-to-left,
.animation-reveal__load--left-to-right {
    overflow: hidden;

    &::before,
    &::after {
        left: 0;
        right: 0;
        transform: translateX(100%);
    }

    .animation-reveal__content {
        animation: content-reveal 0s linear ($animation-duration/1.2) forwards;
    }

    &::before,
    &::after {
        animation-name: video-slide--left-to-right;
    }
}

.animation-reveal__load--left-to-right {
    &::before,
    &::after {
        animation-direction: reverse;
    }
}

@keyframes content-reveal {
    0% {
        opacity: 0;
    }
    100% {
        opacity:1;
    }
}

@keyframes video-slide--left-to-right {
    0% {
        transform: translateX(100%);
    }
    50% {
        transform: translateX(0%);
    }
    100% {
        transform: translateX(-100%);
    }
}
