.feature-events {
    margin: 30px 0;
    position:relative;

    @include media("above:medium") {
        margin-bottom: 100px;
    }
}

.feature-events__cards__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        position: relative; 
}

.feature-events__cards__content {
    @include grid-column(4); 
}

.feature-events__cards__content__item {  
    box-shadow: 0 0 66px 3px rgba(0, 0, 0, 0.15);
    padding: $measure-large;
    
    @include media("below:medium") {
        margin: $measure-medium 0;
    }

    @include media("above:medium") {
        margin: 0 $measure-medium;
        height: 100%;
    }

    time {
        display: block;
        margin-bottom: 20px;
        font-weight: 100;
    }
}

.feature-events__cards__link {
    a {
        @include anchor(skate);
    }
    position: absolute;
    bottom: -60px;
    right: 40px;
    @include media("above:large") {
        bottom: -100px;
    }
}
.feature-events__cards__content {
    a {
        @include anchor(skate);
    }
}

