.block-links__bg {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background-color: rgba(0,0,0,0.5);
    }
}

.block-links__items {
    color: $color-white;
    margin: 100px 0;
    padding: 10px;
    z-index: 2;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
    @include media("above:large") {
        padding: 10px 100px;
    }
}

.block-links__items--blue {
    background-color: $color-prussian;
}

.block-links__items--red {
    background-color: $color-alizarin;
}

.block-links__items__anchor {
    display: block;
    width: 300px;
    padding: 10px;
}

.block-links__items__anchor__header {
    padding-bottom: $measure-medium;
    overflow-x: hidden;
    font-size: $font-size-gamma;
}

.block-links__items__anchor__text {
    display: none;
    @include media("above:medium") {
        display: block;
    }
}

.block-links__items__anchor__header__icon {
    display: inline-block;
    position: absolute;
    top: 10px;
    right: 0;
    font-weight: $font-weight-light;
}

